<template>
	<b-card-group class="row" id="result">
		<b-card class="col-sm-12" overlay
			:img-src="toBackgroundImage(selected_activity)"
			text-variant="danger"
			:title="selected_activity.name" style="margin:0px;">
			<router-link to="anime"><b-button variant="warning"><i class="fa fa-arrow-left fa-5x" aria-hidden="true"></i></b-button></router-link>
			<router-link to="start"><b-button variant="primary"><i class="fa fa-home fa-5x" aria-hidden="true"></i></b-button></router-link>
			<my-chart :chart-data="datacollection" :options="options"></my-chart>
			<b-table :items="weather_icons" :fields="fields">
				<template slot="day_ind" scope="row">{{row.value}}</template>
				<template slot="day1" scope="row"><img :src="toWeatherIcon(row.value)" height="10%"/></template>
				<template slot="day2" scope="row"><img :src="toWeatherIcon(row.value)" height="10%"/></template>
				<template slot="day3" scope="row"><img :src="toWeatherIcon(row.value)" height="10%"/></template>
			</b-table>
		</b-card>
	</b-card-group>
</template>

<script>
import VueCharts from 'vue-chartjs';
import {mapState} from 'vuex';
const { reactiveProp } = VueCharts.mixins;

Chart.defaults.global.defaultFontColor = '#f0f';
Chart.defaults.global.defaultFontStyle = 'bold';

export default {
	components: {
		//Pie, Radar, Doughnut, Line, PolarArea, Bubble, Bar, HorizontalBar, Scatter
		MyChart: VueCharts.Radar.extend({
  			mixins: [reactiveProp],
  			props: ['options'],
			mounted(){ this.renderChart(this.chartData, this.options); }
		})
	},
	data () {
		return {
			lines: 2,
			//count: 12,
			values: [],
			datacollection: null,
			options: {
				fontColor: '#f0f',
				legend: {
					display: true,
					onClick: (e) => {},
					labels: {
						fontColor: '#f0f',
						fontStyle: 'bold'
					},
					title: {
						fontColor: '#f0f',
						fontStyle: 'bold'
					}
				},
				title: { display: false }
			},
			fields:{
				day_ind: {label: 'day/night'},
				day1: {label: 'today'},
				day2: {label: 'tomorrow'},
				day3: {label: '2days later'}
			},
			weather_icons: []
		}
	},
	computed: mapState(['selected_activity', 'weathers']),
	mounted () {
		window.scroll(0, 0);
		if(!this.$store.state.selected_activity || !this.$store.state.selected_activity.recommend_detail){
			console.log('selected activity is nothing...');
			this.$router.push('start');
		}else{
			this.fillData();

			const d = this.$store.state.weathers.filter((v) => v.day_ind === 'D');
			const n = this.$store.state.weathers.filter((v) => v.day_ind === 'N');
			this.weather_icons = [];
			this.weather_icons.push({
				day_ind: 'day',
				day1: d[0].icon_code,
				day2: d[1].icon_code,
				day3: d[2].icon_code
			});
			this.weather_icons.push({
				day_ind: 'night',
				day1: n[0].icon_code,
				day2: n[1].icon_code,
				day3: n[2].icon_code
			});
		}
	},
	methods: {
		toBackgroundImage(act){
			const image = require.context('../assets/', false, /\.jpg$/);
			try{
				return image(`./${act.recommend_detail.activity_id}.jpg`);
			}catch(e){
				console.log(e);
				return image('./question.jpg');
			}
			/*const image = require.context('../assets/', false, /\.svg$/);
			try{
				return image(`./${act.recommend_detail.activity_id}.svg`);
			}catch(e){
				return image('./question.jpg');
			}*/
		},
		toWeatherIcon(code){
			return require.context('../assets/weathericons/', false, /\.png$/)(`./icon${code}.png`);
		},
		//inc(){ this.count++; this.fillData(); },
		//dec(){ this.count--; this.fillData(); },
		//incdata(){ this.lines++; this.fillData(); },
		//decdata(){ this.lines--; this.fillData(); },
		start(){ this.$router.push('start'); },
		fillData () {
			let sets = [];
			let labels = [];
			const ratios = this.selected_activity.recommend_detail.recommend_ratio_detail;
			for(let name in ratios){
				labels.push(this.$store.state.big5jpname[name]);
			}
			for(let j=0; j<this.lines; j++){
				let arr = [];
				for(let name in ratios){
					if(j === 0){
						arr.push(this.$store.state.personalities[name]);
					}else{
						arr.push(ratios[name]);
					}
				}

				let v = {};
				v.label = (j === 0) ? 'you' : 'others';
				let colors = [];
				//for(let i=0; i<Object.keys(ratios).length; i++){
				//	colors.push(`hsla(${360.0 * i / Object.keys(ratios).length}, ${100 * (this.lines - j) / this.lines}%, 50%, 0.5)`);
				//}
				//v.backgroundColor = colors;
				v.backgroundColor = 'hsla(' + 360.0 * j / this.lines + ', 100%, 50%, 0.5)';
				v.data = arr;
				sets.push(v);
			}

			this.datacollection = {
				labels: labels,
				datasets: sets,
				pointLabelFontColor: 'white',
				scaleFontColor: 'white',
				responsive: true
			}
		}
	}
}
</script>

<style>
#result {
	height: 100vh;
	width: 100vw;
}

.card-img {
	height: 100%;
	width: 100%;
	filter: grayscale(50%) blur(2px);
}
/*.small {
	max-width: 600px;
	margin:  150px auto;
}*/
</style>



