<template>
	<div class="anime">
		<b-navbar variant="info" type="light">
			<b-navbar-brand tag="h1">Your follow wind is ...</b-navbar-brand>
		</b-navbar>
		<div class="container">
		<div v-if="icons.length == 0">
			<h1>no activities found...</h1>
			<router-link to="start"><b-button variant="primary"><i class="fa fa-home fa-5x" aria-hidden="true"></i></b-button></router-link>
		</div>
	    <!--b-button @click="show = !show"> Toggle render </b-button>
	    <b-button @click="result"> next </b-button-->
		<!--transition name="custom-classes-transition" enter-active-class="animated tada" leave-active-class="animated bounceOutRight" >
			<p v-if="show"><i class="fa fa-bicycle fa-5x" aria-hidden="true"></i></p>
		</transition>
		<transition name="custom-classes-transition" enter-active-class="animated bounceInDown" leave-active-class="animated bounceOutDown" >
			<p v-if="show"><i class="fa fa-coffee fa-5x" aria-hidden="true"></i></p>
		</transition>
		<transition name="custom-classes-transition" enter-active-class="animated slideInLeft" leave-active-class="animated slideOutRight" >
			<p v-if="show"><i class="fa fa-cutlery fa-5x" aria-hidden="true"></i></p>
		</transition-->

		<transition-group name="list">
			<div v-for="icon in icons" :key="icon.id" class="col-sm-12 list-item">
				<img @click="result(icon)" :v-show="icon.show" :id="icon.id" :src="icon.image" width="33%" height="auto" :style="icon.style"/>
			</div>
		</transition-group>
		</div>
		<b-navbar variant="secondary" type="light" v-show="showCopyright">
			<b-nav-text>Icons made by <a href="http://www.freepik.com" title="Freepik">Freepik</a> from <a href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a> is licensed by <a href="http://creativecommons.org/licenses/by/3.0/" title="Creative Commons BY 3.0" target="_blank">CC 3.0 BY</a></b-nav-text>
		</b-navbar>
  </div>
</template>

<script>
import Vue from 'vue';
const wait = (millis) => new Promise((resolve) => window.setTimeout(resolve, millis));
class Icon{
	constructor(args){
		this.id = args.screen_id;
		this.activity_id = args.recommend_detail.activity_id;
		this.name = args.activity_detail.activity_name;
		this.hue = (args.is_recommend) ? 240 : 0;
		this.size = `${args.size}%`;
		this.grayscale = `${args.grayscale}%`;
		this.show = true;
		this.origin = args;
	}

	get style (){
		return `filter:hue-rotate(${this.hue}deg) grayscale(${this.grayscale});`;
	}

	get image (){
		var images = require.context('../assets/', false, /\.svg$/);
		try{
			return images('./' + this.activity_id + '.svg');
		}catch(e){
			console.log('画像無し：' + this.activity_id + '.svg');
			return images('./question.svg');
		}
	}
}

export default {
	name: 'Anime',
	data () {
		return {
			show: true,
			icons: []
		};
	},
	mounted () {
		window.scroll(0, 0);
		//this.icons = this.response.map((r) => new Icon(r));
		this.icons = [];
		/*this.$store.state.activities.forEach(async (r) => {
			await wait(1000);
			console.log(r);
			this.icons.push(new Icon(r));
		});*/
		this.$store.state.activities.reduce((left, right) => {
			return left.then(() => {
				this.icons.push(new Icon(right));
				return wait(200);
			});
		}, wait(0));

		//this.$store.state.activities.map((r) => new Icon(r));
		/*
		let deg = 0;
		const timer = window.setInterval(() => {
			this.iconstyle = `filter:hue-rotate(${deg}deg);`;
			deg+=10;
			if(deg>360) window.clearInterval(timer);
		}, 100);
		*/
	},
	methods: {
		result: async function(icon){
			this.icons = this.icons.filter((i) => i.id !== icon.id);
			icon.show = false;
			//this.icons = this.icons.slice();
			await wait(500);
			await this.$store.dispatch('selectActivity', icon.id);
			this.$router.push('resultradar');
		},
		showCopyright: function(){
			return this.icons.length === this.$store.state.activities.length;
		},
		toURL: function(icon){
			return icon.image;
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.v-transition {
	transition: all 0.7s ease;
	transform: scale3d(1, 1, 1);
}

.v-enter, .v-leave {
    transform: scale3d(0, 0, 1);
    opacity: 0;
}

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

.list-item {
	transition: all 1s;
	display: inline-block;
}
.list-enter {
	opacity: 0;
	transform: scale(0.0) translateY(-200px);
}
.list-leave-to {
	opacity: 0;
	transform: scale(4.0);
}

/*.list-leave-active {
	position: absolute;
}*/
</style>



