<template>
	<div class="start row">
		<b-form-group
			id="twitter"
			description="Let us know your twitter account."
			label="Enter your Twitter ID"
			:feedback="feedback" 
			:state="twitter_state"
			class="col-sm-12">
			<b-input-group>
				<b-input-group-button>
					<b-btn variant="info" @click="next">@</b-btn>
				</b-input-group-button>
				<b-form-input id="input1" :state="twitter_state" v-model.trim="twitter_id"></b-form-input>
			</b-input-group>
			<!--b-button v-on:click='a' v-bind:value='msg' variant='danger'>{{msg}}</b-button-->
		</b-form-group>
		<br/>
		<router-link to="map" class="col-sm-12"><b-button @click="next" variant="primary"><i class="fa fa-twitter fa-5x" aria-hidden="true"></i><i class="fa fa-arrow-right fa-5x" aria-hidden="true"></i></b-button></router-link>
		<span class="col-sm-12">&nbsp;</span>
	</div>
</template>

<script>
import {mapState} from 'vuex';
export default {
	name: 'Start',
	computed: {
		feedback() {
			return this.name.length > 0 ? '' : 'Please enter your twitter account.';
		},
		twitter_state() {
			return this.name.length > 0 ? 'valid' : 'invalid';
		},
		twitter_id:{
			get(){
				return this.$store.state.twitter_id;
			},
			set(value){
				this.$store.commit('updateTwitterID', value);
			}
		}
		//...mapState(['twitter_id'])
	},
	mounted () {
		window.scroll(0, 0);
	},
	data () {
		return {
			name: '',
			msg: 'HOGEHOGE'
		};
	},
	methods: {
		next: function(){
			this.$router.push('map');
		},
		a: function(){
			var x = async () => {};
			x();
			console.log('hogehoge');
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.start{
	height:100vh;
	-webkit-align-items:center;
	align-items:center;
}
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



