<template>
	<div class="row">
		<b-jumbotron lead="We discover your follow wind..." class="col-sm-12" variant="info">
			<!--transition name="slide-fade">
				<b-btn v-show="show" variant="success" @click="next"><i class="fa fa-arrow-right fa-5x" aria-hidden="true"></i></b-btn>
			</transition-->
			<b-button :disabled="!show" variant="primary" @click="next">
				<i v-show="!show" class="fa fa-spinner fa-spin fa-5x fa-fw"></i>
				<i v-show="show" class="fa fa-arrow-right fa-5x" aria-hidden="true"></i>
			</b-button>
			<p v-if="hasError">
				Server Error<br/>
				<router-link to="start"><b-button variant="info"><i class="fa fa-home fa-5x"></i></b-button></router-link>
				<pre style="text-align:left; margin-left:0px; width:100%; overflow:auto; white-space:pre-wrap; word-wrap:break-word;">{{JSON.stringify(error, {}, 2).replace(/\\n/g, '\n')}}</pre>
			</p>
		</b-jumbotron>
		<b-progress v-model="counter" :precision="0" class="col-sm-12 mb-3" :show-progress="true" :animated="true"></b-progress>
		<b-list-group class="col-sm-12">
			<!--transition-group enter-active-class="animated fadeInUp" leave-active-class="animated fadeOutRight" name="tweet"-->
			<transition-group name="list-complete">
				<b-list-group-item v-for="timeline in visibleTimelines" v-bind:key="timeline" style="text-align:left;" class="list-complete-item">{{timeline}}</b-list-group-item>
			</transition-group>
		</b-list-group>
	</div>
</template>

<script>
import {mapState} from 'vuex';
import Vue from 'vue';
const wait = (millis) => new Promise((resolve) => setTimeout(resolve, millis));
const nextTick = () => new Promise((resolve) => Vue.nextTick(resolve));
export default {
	name: 'Progress',
	mounted:async function() {
		window.scroll(0, 0);
		this.show = false;
		this.hasError = false;
		try{
			this.counter = 20;
			const response = await this.$store.dispatch('invokeTwitter', {});
			this.counter = 50;
			this.showTimelines(response.tweets);
			await this.$store.dispatch('invokeAPI', {});
			this.counter = 100;
			this.show = true;
		}catch(e){
			console.log(e);
			this.hasError = true;
			this.error = JSON.stringify(e, {}, 2);
		}
	},
	data () {
		return {
			show: false,
			hasError: false,
			error: null,
			counter: 0,
			visibleTimelines:[]
		};
	},
	methods:{
		showTimelines: async function(arr){
			this.$data.visibleTimelines = [];
			for(let timeline of arr){
				this.$data.visibleTimelines.push(timeline);
				await wait(800);
				if(this.$data.visibleTimelines.length > 1){
					this.$data.visibleTimelines.shift()
				}
				await wait(400);
			}
		},
		next: function(){
			this.$router.push('anime');
		}
	}
};
</script>

<style>
.list-complete-item {
	transition: all 1s;
	display: inline-block;
}
.list-complete-enter {
	opacity: 0;
	transform: translateY(30px);
}
.list-complete-leave-to {
	opacity: 0;
	transform: translateX(300px);
}

.list-complete-leave-active {
	position: absolute;
}

/* enter、 leave アニメーションで異なる間隔やタイミング関数を利用することができます */
.slide-fade-enter-active {
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-leave-active {
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}


.tweet-item {
	transition: all 1s;
	display: inline-block;
}
.tweet-move {
	transform: 1s;
}
</style>



