<template>
	<div class="map row">
		<b-form-input class="col-sm-12 form-control" type="date" v-model="ymd" id="example-date-input"/>
		&nbsp;<br/>
		<b-input-group size="lg" class="col-sm-12">
			<b-form-input v-model="addr_tmp"></b-form-input>
			<b-input-group-button>
				<b-button @click="geo" variant="warning"><i class="fa fa-location-arrow" aria-hidden="true"></i></b-button>
			</b-input-group-button>
		</b-input-group>
		<b-form-text class="text-center col-sm-12">Please enter the place you want to visit, or press <i class="fa fa-location-arrow fa-1x"></i> button.</b-form-text>
		<b-alert class="col-sm-12" variant="danger" dismissible :show="errmsg != ''" @dismissed="errmsg=''"><pre>{{errmsg}}</pre></b-alert>
		<transition enter-active-class="animated fadeInDown">
			<div class="col-sm-12" v-if="options_addr.length > 0">
				<b-form-select v-model="addr" :options="options_addr" v-on:input="changeAddr" class="mb-3"></b-form-select>
			</div>
		</transition>
		<!--div class="row">
			<span class="col-sm-1">&nbsp;</span>
			<span class="col-sm-3">緯度：{{lat}}</span>
			<span class="col-sm-3">経度：{{lng}}</span>
			<span class="col-sm-3">精度：{{acc}}</span>
		</div-->
		<div class="col-sm-12">
			<b-button @click="next" variant="primary"><i class="fa fa-arrow-right fa-5x" aria-hidden="true"></i></b-button>
		</div>
		<b-modal v-model="in_progress" :hide-header='true' :hide-footer='true'>
			<p>measuring current position...</p>
			<img src="../assets/progress_circle2.gif"/>
		</b-modal>
		<span class="col-sm-12">&nbsp;</span>
		<span class="col-sm-12">&nbsp;</span>
	</div>
</template>

<script language="text/javascript">
import axios from 'axios';
import {mapState} from 'vuex';
export default {
	name: 'Map',
	mounted: function(){
		window.scroll(0,0);
		const now = new Date();
	},
	computed: {
		ymd: {
			get(){
				return this.$store.getters.formattedDate;
			},
			set(value){
				this.$store.commit('updateDate', value);
			}
		},
		addr: {
			get(){
				return this.$store.addr;
			},
			set(value){
				//this.$store.commit('updateAddr', value);
				this.$store.dispatch('asyncAddr', value);
			}
		}
	},
	data () {
		return {
			lat:0,
			lng:0,
			acc:0,
			addr_tmp:this.$store.state.addr,
			selected_addr: null,
			options_addr:[],
			in_progress:false,
			errmsg: ''
		};
    },
	methods: {
		next: function(){
			this.$store.dispatch('asyncAddr', this.addr_tmp);
			this.$router.push('Progress');
		},
		changeAddr: function(data){
			this.addr_tmp = data;
			//this.$store.dispatch('asyncAddr', data);
			//console.log(data);
		},
		geo: function(){
			this.in_progress = true;
			if('geolocation' in window.navigator){
				console.log('geolocation is available.');
			}else{
				console.log('geolocation is not available.');
			}
			/*
			console.log(window.navigator.geolocation);
			console.log(window.navigator.permissions);
			navigator.permissions.query({
				name: 'geolocation'
			}).then((permission) => {
				console.log(permission);
				switch (permission.state) {
					case 'granted':
						navigator.geolocation.getCurrentPosition(
							(pos) => resolve(pos.coords),
							(error) => console.error(`Error Code: ${error.code} - ${error.message}`)
						);
						break;
					case 'prompt':
						console.info('Check Geolocation Promt.');
						navigator.geolocation.getCurrentPosition(
							(pos) => resolve(pos.coords),
							(error) => console.error(`Error Code: ${error.code} - ${error.message}`)
						);
						break;
					case 'denied':
					default:
						console.log('denied');
						resolve(null);
						break;
				}
			}).catch((err) => console.log(err));
			*/

			window.navigator.geolocation.getCurrentPosition(async (position) => {
				this.errmsg = '';
				this.lat = position.coords.latitude;
				this.lng = position.coords.longitude;
				this.acc = position.coords.accuracy;
				console.log(position);
				const response = await axios.get('https://maps.googleapis.com/maps/api/geocode/json', {
					params:{
						latlng:`${this.lat},${this.lng}`,
						sensor:false
					}
				});
				console.log(response);
				if(response.data.results.length > 0){
					this.options_addr = response.data.results.map((r) => ({text:r.formatted_address, value:r.formatted_address}));
					//this.selected_addr = this.options_addr[0].formatted_address;
					this.addr = this.options_addr[0].formatted_address;
					//this.$store.commit('updateAddr', this.options_addr[0].formatted_address);
				}
				this.in_progress = false;
			}, (err) => {
				this.in_progress = false;
				this.errmsg = JSON.stringify(err, {}, 2);
				console.log(err);
			});
			/*setTimeout(() => {
				console.log('clear watch.');
				window.navigator.getlocation.clearWatch(id);
			}, 10000);*/
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.map{
	height:100vh;
	align-items:center;
}

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



