<template>
	<div class="small">
		<div>{{selected_activity.activity_detail.activity_name}}</div>
		<b-button variant="success" @click="fillData()">Randomize</b-button>
		<b-button @click="inc()">add label</b-button>
		<b-button @click="dec()">sub label</b-button>
		<b-button @click="incdata()">add data</b-button>
		<b-button @click="decdata()">sub data</b-button>
		<my-chart :chart-data="datacollection"></my-chart>
	</div>
</template>

<script>
import VueCharts from 'vue-chartjs';
import {mapState} from 'vuex';
const { reactiveProp } = VueCharts.mixins;

export default {
	components: {
		//Pie, Radar, Doughnut, Line, PolarArea, Bubble, Bar, HorizontalBar, Scatter
		MyChart: VueCharts.Doughnut.extend({
  			mixins: [reactiveProp],
  			props: ['options'],
			mounted(){ this.renderChart(this.chartData, this.options); }
		})
	},
	data () {
		return {
			lines: 1,
			count: 12,
			values: [],
			datacollection: null
		}
	},
	computed: mapState(['selected_activity']),
	mounted () {
		this.fillData();
	},
	methods: {
		inc(){ this.count++; this.fillData(); },
		dec(){ this.count--; this.fillData(); },
		incdata(){ this.lines++; this.fillData(); },
		decdata(){ this.lines--; this.fillData(); },
		fillData () {
			let sets = [];
			let labels = [];
			for(let i=0; i<this.count; i++){
				labels.push('Label ' + i);
			}
			for(let j=0; j<this.lines; j++){
				let arr = [];
				for(let i=0; i<this.count; i++){
					arr.push(this.getRandomInt());
					//arr.push({x:this.getRandomInt(), y:this.getRandomInt(), r:this.getRandomInt()});
				}

				let v = {};
				v.label = 'Data ' + j;
				let colors = [];
				for(let i=0; i<this.count; i++){
					colors.push(`hsla(${360.0 * i / this.count}, ${100 * (this.lines - j) / this.lines}%, 50%, 0.5)`);
				}
				//v.backgroundColor = 'hsla(' + 360.0 * j / this.lines + ', 100%, 50%, 0.5)';
				v.backgroundColor = colors;
				v.data = arr;
				sets.push(v);
			}

			this.datacollection = {
				labels: labels,
				datasets: sets 
			}
		},
		getRandomInt () {
			return Math.floor(Math.random() * (50 - 5 + 1)) + 5;
		}
	}
}
</script>

<style>
.small {
	max-width: 600px;
	margin:  150px auto;
}
</style>



